*, :before, :after {
  box-sizing: border-box;
  border: 0;
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  vertical-align: top;
}

html {
  --header-color: #00acdc;
}

body {
  font-family: Roboto, sans-serif;
}

.header__container {
  min-width: 1024px;
  padding: 0 50px;
}

.header__top {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo {
  display: inline-block;
}

@media (width <= 1024px) {
  .logo {
    margin-right: 243px;
  }
}

.menu__list {
  justify-content: space-between;
  display: flex;
}

.menu__item:not(:last-child) {
  margin-right: 20px;
}

.menu__link {
  text-transform: uppercase;
  height: 60px;
  padding: 22.5px 0;
  font-size: 12px;
  font-weight: 500;
  display: inline-block;
}

.menu__link:hover {
  color: var(--header-color);
}

.is-active {
  color: var(--header-color);
  position: relative;
}

.is-active:after {
  content: "";
  z-index: 1;
  background-color: var(--header-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

